  src: url("../fonts/montserrat-700.woff2") format("woff2"), url("../fonts/montserrat-700.woff") format("woff");
}
*, *::before, *::after {
  box-sizing: border-box;
}
*:focus-visible {
  outline: 2px solid;
  outline-offset: 2px;
}
@supports not selector(:focus-visible) {
  *.focus-visible:focus {
    outline: 2px solid;
    outline-offset: 2px;
  }
}

html {
  min-width: 320px;
  scroll-behavior: smooth;
  font-size: 16px;
  line-height: 1.5;
  font-family: "Montserrat", "Arial", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  color: #011c40;
}

body {
  margin: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

select,
textarea,
input:matches([type=email], [type=number], [type=password], [type=search], [type=tel], [type=text], [type=url]),
button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  color: inherit;
  opacity: 0.7;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

textarea {
  resize: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}
input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}
input::-webkit-search-decoration, input::-webkit-search-cancel-button, input::-webkit-search-results-button, input::-webkit-search-results-decoration {
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}
input[type=number]::-webkit-outer-spin-button, input[type=number]::-webkit-inner-spin-button {
  appearance: none;
}

:disabled {
  opacity: 0.5;
  pointer-events: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

@media (max-width: 767px) {
  .mobile-hide {
    display: none;
  }
}

@media (min-width: 768px) {
  .mobile-show {
    display: none;
  }
}

.container {
  width: 100%;
  max-width: 1250px;
  margin-right: auto;
  margin-left: auto;
  padding: 0 45px;
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.btn {
  padding: 9px 20px 10px;
  font-weight: 600;
  font-size: 14px;
  line-height: 1.5;
  font-family: "Montserrat", "Arial", sans-serif;
  text-align: center;
  overflow-wrap: anywhere;
  color: #011c40;
  background-color: #f9fbfd;
  border: none;
  border-radius: 0;
  cursor: pointer;
  user-select: none;
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .btn:hover, .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn:focus-visible {
  opacity: 0.8;
}
@supports not selector(:focus-visible) {
  .btn.focus-visible:focus {
    opacity: 0.8;
  }
}
@media (hover: hover) {
  .btn:active, .btn:hover:active, .btn.focus-visible:active {
    opacity: 0.5;
  }
}

.firstscreen {
  position: relative;
  color: #f9fbfd;
  background-color: #011c40;
  z-index: 0;
}
@media (max-width: 767px) {
  .firstscreen .is-active + .firstscreen__promo {
    margin-top: 63px;
    opacity: 0;
  }
  .firstscreen .no-js + .firstscreen__promo {
    margin-right: -15px;
    margin-left: -15px;
    padding-right: 15px;
    padding-left: 15px;
  }
}

.firstscreen__promo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 542px;
  padding: 34px 95px 150px;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  transition: opacity 0.3s ease;
}
@media (max-width: 1023px) {
  .firstscreen__promo {
    min-height: 560px;
    padding: 64px 0 150px;
  }
}
@media (max-width: 767px) {
  .firstscreen__promo {
    min-height: 437px;
    padding: 30px 0 96px;
  }
}
.firstscreen__promo h1 {
  margin: 0 0 12px;
  font-weight: 700;
  font-size: 54px;
  line-height: 66px;
}
@media (max-width: 1023px) {
  .firstscreen__promo h1 {
    font-size: 50px;
    line-height: 61px;
  }
}
@media (max-width: 767px) {
  .firstscreen__promo h1 {
    font-size: 36px;
    line-height: 45px;
  }
}
.firstscreen__promo p {
  margin: 0;
  font-size: 20px;
  line-height: 24px;
}
@media (max-width: 1023px) {
  .firstscreen__promo p {
    font-size: 18px;
    line-height: 22px;
  }
}
@media (max-width: 767px) {
  .firstscreen__promo p {
    font-size: 16px;
    margin-right: 58px;
  }
}

.firstscreen__image {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  object-fit: cover;
  width: 100%;
  height: 100%;
}
@media (min-width: 1920px) {
  .firstscreen__image {
    object-position: 50% 60%;
  }
}

