}

.page-header {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  align-content: flex-start;
  padding: 34px 0;
}
@media (max-width: 1023px) {
  .page-header {
    padding: 21px 0 20px;
  }
}
@media (max-width: 767px) {
  .page-header {
    right: -15px;
    left: -15px;
    width: calc(100% + 30px);
    padding: 20px 15px;
    box-shadow: -35px 0 0 transparent;
  }
}

.page-header__toggler {
  display: none;
  padding: 0;
  color: inherit;
  background-color: transparent;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .page-header__toggler:hover, .page-header__toggler:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.page-header__toggler:focus-visible {
  opacity: 0.8;
}
@supports not selector(:focus-visible) {
  .page-header__toggler.focus-visible:focus {
    opacity: 0.8;
  }
}
@media (hover: hover) {
  .page-header__toggler:active, .page-header__toggler:hover:active, .page-header__toggler.focus-visible:active {
    opacity: 0.5;
  }
}
.page-header__toggler::before {
  content: "";
  width: 25px;
  height: 22px;
  background-color: currentColor;
  mask: url("../img/sprite.svg#icon-burger") no-repeat center/100%;
}
@media (max-width: 767px) {
  .page-header__toggler {
    display: flex;
  }
}

.page-header__nav {
  width: 90%;
}
@media (max-width: 767px) {
  .page-header__nav {
    display: none;
    width: 100%;
    margin-top: 32px;
  }
}

.page-header__nav-list {
  justify-content: flex-end;
  margin: 0;
}

@media (max-width: 767px) {
  .page-header.no-js,
.page-header.is-active {
    color: #011c40;
    background-color: #f9fbfd;
  }
  .page-header.no-js .page-header__nav,
.page-header.is-active .page-header__nav {
    display: block;
  }
  .page-header.no-js .page-header__toggler {
    display: none;
  }
  .page-header.is-active {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 35px;
    z-index: 1;
    align-items: flex-start;
    width: calc(100% - 35px);
    overflow-y: auto;
    box-shadow: -35px 0 0 rgba(0, 43, 102, 0.5);
    transition: color 0.3s ease, background-color 0.3s ease, box-shadow 0.3s ease;
  }
  .page-header.is-active .page-header__toggler::before {
    width: 13px;
    margin-left: auto;
    mask: url("../img/sprite.svg#icon-close") no-repeat center/100%;
  }
