}
@media (hover: hover) {
  .form__group input:not([type=checkbox]):hover, .form__group input:not([type=checkbox]):hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.form__group input:not([type=checkbox]):focus-visible {
  opacity: 0.8;
}
@supports not selector(:focus-visible) {
  .form__group input:not([type=checkbox]).focus-visible:focus {
    opacity: 0.8;
  }
}
.form__group [type=checkbox] {
  appearance: none;
  display: flex;
  flex-shrink: 0;
  width: 18px;
  height: 18px;
  color: #011c40;
  background-color: #f9fbfd;
  user-select: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .form__group [type=checkbox]:hover, .form__group [type=checkbox]:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.form__group [type=checkbox]:focus-visible {
  opacity: 0.8;
}
@supports not selector(:focus-visible) {
  .form__group [type=checkbox].focus-visible:focus {
    opacity: 0.8;
  }
}
@media (hover: hover) {
  .form__group [type=checkbox]:active, .form__group [type=checkbox]:hover:active, .form__group [type=checkbox].focus-visible:active {
    opacity: 0.5;
  }
}
