  content: "";
  margin: auto;
  width: 12px;
  height: 8px;
  background-color: #011c40;
  mask: url("../img/sprite.svg#icon-check") no-repeat center/100%;
}
.form__group label {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  font-size: 10px;
